export function transform(data) {
  let arr = [0, 0, 0, 0, 0, 0, 0, 0, 0]
  data.forEach(item => {if(item >=1 && item <= 9) arr[item - 1] += 1})

  const max = Math.max(...arr)
  let results = []
  arr.forEach((value, index) => {
    if(value >= max) {
      results.push({
        type: index + 1,
        count: value,
        nature: natures[index]
      })
    }
  })
  return results
}

export function trans_name(user, role) {
  const maps = {self: '同学', dad: '爸爸', mom: '妈妈'}
  const username = user.get('username')
  return username.replace(/-.+$/, '') + maps[role]
}



