<template lang="pug">
div
  .item
    | 我是
    input(v-model.trim="grade" type="text" spellcheck="false" placeholder="联考1班 | 校考1班")
    input(v-model.trim="name" type="text" spellcheck="false" placeholder="孩子姓名")
    | 同学的
  .roles
    span(:class="{checked: role == 'dad'}")
      input(type="radio" value="dad" id="dad" v-model="role")
      label(for="dad") 父 亲
    span(:class="{checked: role == 'mom'}")
      input(type="radio" value="mom" id="mom" v-model="role")
      label(for="mom") 母 亲
  button(@click="start" :disabled="loading") {{loading ? '进入中...' : '开 始'}}
</template>

<script>
import {login} from '@/api/users'

export default {
  data() {
    return {
      name: '',
      grade: '',
      role: '',
      loading: false
    }
  },
  methods: {
    start() {
      const {name, grade, role} = this

      if(!name || !grade || !role) return alert('所有字段必填')
      if(/\w/.test(name)) return alert('名字必须为中文')
      if(!(/^联考[1-5]班$/).test(grade) && !(/^校考[1-30]班$/).test(grade)) return alert('请按格式填写班级(联考1～5班或校考1～30班)')

      this.loading = true
      login(`${name}-${grade}`, role)
        .then(user => {
          const n = user.get(role).length + 1
          this.loading = false
          this.$router.push(`/${role}/topics/${n}`)
        })
        .catch(err => {
          this.loading = false
          alert(err)
        })
    }
  }
}
</script>

<style lang="css" scoped>
.item {
  font-size: 18px;
  text-align: center;
  letter-spacing: 3px;
}
.item input {
  display: block;
  width: 60%;
  margin: 12px auto;
  text-align: center;
}
.roles {
  display: flex;
  justify-content: space-around;
  font-size: 20px;
}
.roles span {
  padding: 0 8px;
  border: solid 1px #bae7ff;
  border-radius: 4px;
}
.roles span input {
  display: none;
}
.checked {
  color: #333;
  background-color: #bae7ff;
}
button {
  width: 100%;
  margin-top: 240px;
  text-align: center;
  font-size: 24px;
}
</style>



