<template lang="pug">
div
  .item
    label(for="name") 姓名：
    input(id="name" v-model.trim="name" type="text" spellcheck="false" placeholder="你的名字")
  .item
    label(for="grade") 班级：
    input(id="grade" v-model.trim="grade" type="text" spellcheck="false" placeholder="联考1班 | 校考1班")
  button(@click="start" :disabled="loading") {{loading ? '进入中...' : '开 始'}}
</template>

<script>
import {login} from '@/api/users'

export default {
  data() {
    return {
      name: '',
      grade: '',
      loading: false,
    }
  },
  methods: {
    start() {
      const {name, grade} = this

      if(!name || !grade) return alert('所有字段必填')
      if(/\w/.test(name)) return alert('名字必须为中文')
      if(!(/^联考[1-5]班$/).test(grade) && !(/^校考[1-30]班$/).test(grade)) return alert('请按格式填写班级(联考1～5班或校考1～30班)')

      this.loading = true
      login(`${name}-${grade}`, 'self')
        .then(user => {
          const n = user.get('self').length + 1
          this.loading = false
          this.$router.push('/self/topics/' + n)
        })
        .catch(err => {
          this.loading = false
          alert('请正确填写各项信息')
        })
    }
  }
}
</script>

<style lang="css" scoped>
.item {
  width: 80%;
  margin: auto;
  margin-top: 16px;
  display: flex;
}
.item input {
  flex-grow: 1;
}
button {
  width: 100%;
  margin-top: 240px;
  text-align: center;
  font-size: 24px;
}
</style>



