<template lang="pug">
div
  h1 九型人格测试
  ul
    li 1.这是目前最权威的测试系统，共108个题目
    li 2.请在符合你的题目后面点“是”
    li 3.最后结果中占比最高的就是你的人格类型
    li 4.这只是测试你的人格类型，并无好坏之分
  nav
    .center(v-if="is_login")
      router-link(to="/home") 进入主页
    .bar(v-else)
      router-link(to="/register/parent") <<<家长
      router-link(to="/register/student") 学生>>>
</template>

<script>
import AV from '@/api/AV'

export default {
  computed: {
    is_login() {
      const user = AV.User.current()
      const role = localStorage.role
      return user && role
    }
  }
}
</script>

<style lang="css" scoped>
h1 {
  text-align: center;
  font-size: 24px;
  letter-spacing: 2px;
}
ul {
  margin-left: 8px;
  margin-right: 8px;
  border-top: solid 1px #bae7ff;
  border-bottom: solid 1px #bae7ff;
}
li {
  margin-top: 8px;
  margin-bottom: 8px;
}
nav {
  margin-top: 80px;
}
.center {
  font-size: 20px;
}
</style>



