<template lang="pug">
div
  h2
    span {{topic.n}}.
    span {{topic.content}}
  .bar
    button(@click="submit(0)") 否
    button(@click="submit(topic.type)") 是
</template>

<script>
import AV from '@/api/AV'
import {get} from '@/api/topic'

export default {
  data() {
    return {
      topic: {},
    }
  },
  created() {
    this.get_topic()
  },
  watch: {
    '$route': 'get_topic'
  },
  methods: {
    async submit(type) {
      const user = AV.User.current()
      const role = this.$route.params.role
      const result = user.get(role)
      const n = this.topic.n
      result[n - 1] = type
      user.set(role, result)
      await user.save()
      this.$router.push(`/${role}/topics/${n + 1}`)
    },
    async get_topic() {
      const user = AV.User.current()
      const role = this.$route.params.role
      if(!user) return this.$router.push('/')

      const n = parseInt(this.$route.params.n)
      if (n > 108) return this.$router.push(`/${role}/result`)
      this.topic = await get(n)
    }
  }
}
</script>

<style lang="css" scoped>
h2 {
  font-size: 20px;
  text-align: center;
  padding: 0 8px;
}
.bar {
  justify-content: space-around;
  margin-top: 240px;
}
button {
  width: 56px;
  height: 56px;
  line-height: 56px;
  text-align: center;
  font-size: 24px;
  border: solid 1px #bae7ff;
  border-radius: 50%;
}
button:active {
  color: #333;
  background-color: #bae7ff;
}
</style>



