<template lang="pug">
.main
  template(v-if="user.get($route.params.role).length > 0")
    .title(v-if="is_self") 测试完毕
    .subtitle {{name}}的人格类型为以下综合：
    ul
      li.nature(v-for="(v, i) in results" :key="i")
        h2.nature-title
          span {{v.nature.name}}
          span {{v.type}}号
        p.desc {{v.nature.desc}}
  .subtitle(v-else) {{name}}还没开始测呢...
  .goHome
    router-link(to="/home") 回主页
</template>

<script>
import AV from '@/api/AV'
import {transform, trans_name} from '@/assets/utils'

export default {
  data() {
    return {
      user: AV.User.current(),
    }
  },
  computed: {
    is_self() {
      return localStorage.role == this.$route.params.role
    },
    name() {
      return this.is_self ? '你' : trans_name(this.user, this.$route.params.role)
    },
    results() {
      return transform(this.user.get(this.$route.params.role))
    }
  },
}
</script>

<style lang="css" scoped>
.main {
  padding-bottom: 80px;
}
.title {
  margin-bottom: 0;
  font-size: 24px;
  text-align: center;
  letter-spacing: 2px;
}
.subtitle {
  text-align: center;
  font-size: 18px;
}
.nature {
  margin: 8px 16px 32px;
}
.nature-title {
  display: flex;
  justify-content: space-between;
  margin: 0;
  font-size: 20px;
  border-bottom: solid 2px #bae7ff;
}
.desc {
  margin-top: 8px;
}
.goHome {
  margin-top: 16px;
  text-align: center;
  font-size: 20px;
}
</style>



