import AV from './AV'

export async function login(username, role) {
  const st = await find(username)
  if (st) {
    const user = await AV.User.logIn(username, 'xfkj')
    localStorage.role = role
    return user
  } else {
    const s = new AV.User()
    s.setUsername(username)
    s.setPassword('xfkj')
    const user = await s.signUp()
    localStorage.role = role
    return user
  }
}

function find(username) {
  const users = new AV.Query('_User')
  return users.equalTo('username', username).first()
}



