import Vue from 'vue'
import Router from 'vue-router'
import AV from '@/api/AV'

import Index from '@/components/Index'
import Home from '@/components/Home'
import Topic from '@/components/Topic'
import Result from '@/components/Result'
import Student from '@/components/register/Student'
import Parent from '@/components/register/Parent'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Index
    },
    {
      path: '/home',
      beforeEnter(to, from, next) {
        if (!AV.User.current() || !localStorage.role) next('/')
        else next()
      },
      component: Home
    },
    {
      path: '/register/student',
      component: Student
    },
    {
      path: '/register/parent',
      component: Parent
    },
    {
      path: '/:role/topics/:n',
      component: Topic
    },
    {
      path: '/:role/result',
      beforeEnter(to, from, next) {
        if (!AV.User.current()) next('/')
        else next()
      },
      component: Result
    }
  ]
})



